.App {
    text-align: center;
}

.App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
    height: 80px;
}

.App-header {
    background-color: #222;
    height: 150px;
    padding: 20px;
    color: white;
}

.App-intro {
    font-size: large;
    margin: 0 auto;
    max-width: 1040px;
    width: 100%;
}

.App-btn {
    background-color: #ff9900;
    outline: 0;
    border: none;
    border-radius: 5px;
    color: white;
    font-size: 1.2em;
    padding: 0.5em 1em;
    margin: 1em;
    cursor: pointer;
}

.list-card {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-wrap: wrap;
        flex-wrap: wrap;
    -ms-flex-pack: center;
        justify-content: center;
}

.App-card {
    max-width: 20rem;
    display: -ms-flexbox;
    display: flex;
    border: 1px solid #eaeaea;
    margin: 1rem 0.5em;
    border-radius: 5px;
    -webkit-transition: all .3s ease;
    -o-transition: all .3s ease;
    transition: all .3s ease;
}

.App-card:hover {
    -webkit-box-shadow: 0 0 1px rgba(39, 44, 49, .1), 0 3px 16px rgba(39, 44, 49, .07);
            box-shadow: 0 0 1px rgba(39, 44, 49, .1), 0 3px 16px rgba(39, 44, 49, .07);
    -webkit-transform: translate3D(0, -5px, 0);
        -ms-transform: translate3D(0, -5px, 0);
            transform: translate3D(0, -5px, 0);
}

.App-card-image {
    margin: 0;
    padding: 0;
    width: 100%;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
        flex-direction: column;
}

.App-card-image img {
    margin-top: 0.5em;
    width: 95%;
}

.App-card-footer {
    margin-top: auto;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: row;
        flex-direction: row;
    -ms-flex-align: center;
        align-items: center;
    border-top: 1px solid #eaeaea;
}

.App-card-avatar {
    width: 48px !important;
    height: 48px !important;
    border-radius: 50%;
    margin: 0.5em;
}

@-webkit-keyframes App-logo-spin {
    from {
        -webkit-transform: rotate(0deg);
                transform: rotate(0deg);
    }
    to {
        -webkit-transform: rotate(360deg);
                transform: rotate(360deg);
    }
}

@keyframes App-logo-spin {
    from {
        -webkit-transform: rotate(0deg);
                transform: rotate(0deg);
    }
    to {
        -webkit-transform: rotate(360deg);
                transform: rotate(360deg);
    }
}


